<template id="home">
  <div class="all">
    <Topbar class="history">
      <img src="../assets/img/home/zhgl.png" alt="">
      <span>历史记录</span>
    </Topbar>
    <div class="history-choose clearfix">
      <div class="row row-flex clearfix">
        <div class="date">
          <span class="choose-title choose-title1">日期选择：</span>
          <DatePicker type="date" placeholder="选择日期" :options="options5" :value="dateChangeStart"  @on-change='dateChangeStartFn' style="width: 1.5rem"></DatePicker>
          <span class="choose-title choose-title2">至</span>
          <DatePicker type="date" :options="options4" :value="dateChangeEnd" placeholder="选择日期" @on-change='dateChangeEndFn' style="width: 1.5rem"></DatePicker>
        </div>
        <div class="keyword">
          <span class="keyword-title">病人姓名：</span>
          <input type="text" name="" value="" v-model="name" placeholder="请输入姓名检索">
        </div>
        <a href="javascript:void(0)" class="search" @click='search()'>检索</a>
      </div>
      <div class="row clearfix" v-if="userInfo.role!=2">
        <span class='choose-title'>科室选择：</span>
        <a href="javascript:void(0)" class="choose-btn" @click="active('-1')" :class="activeDepart==-1?'active':''">全部</a>
        <a href="javascript:void(0)" class="choose-btn" @click="active(item.departmentId)" :class="activeDepart==item.departmentId?'active':''" v-for="(item,index) in departmentList">{{item.name}}</a>
      </div>
    </div>
    <div class="main">
      <Table border :columns="columns7" :data="historyList"></Table>

    </div>
    <Page :total="totalPage" :page-size='pageSize' :current='currentPage' @on-change="changePage" class="pages"/>
    <Toast :flow="historyStatus">
      <div class="box history-box">
        <div class="container">
          <img src="../assets/img/monitoring/guanbi.png" class="close" alt="" @click="history()">
          <div class="title clearfix">
            <img src="../assets/img/tw.png" alt="">
            <span>体温记录</span>
          </div>
          <div class="content history-content">
            <div class="zx-pic" id='myChart' style="width:11.45rem;height:4.8rem;"></div>
          </div>
          <div class="ipt-group">
            <a href="javascript:void(0)" class="on" @click='history()'>确认</a>
            <a href="javascript:void(0)" class="off" @click="history()">取消</a>
          </div>
        </div>
      </div>
    </Toast>
  </div>
</template>

<script type="text/javascript">
import {Message} from 'iview'
import Vue from "vue";
var that= {};
import Topbar from '../components/topbar'
import Toast from '../components/toast'
import storage from 'good-storage'
export default {
  data(){
   return{
     historyStatus:false,
     options4: {
         disabledDate (date) {
             return date && date.valueOf() < new Date(that.dateChangeStart) - 86400000;
         }
     },
     options5: {
         disabledDate (date) {
             return date && date.valueOf() > new Date(that.dateChangeEnd) - 86400000;
         }
     },
     animal:'男',
     historyUseTimes:'',
     activeDepart:'-1',
     pageSize:that.global.pageSize,
     currentPage:that.global.currentPage,
     totalPage:that.global.totalPage,
     departmentList:[],
     startDate:'',
     timesList:[],
     temperatureList:[],
     dateChangeStart:'',
     dateChangeEnd:'',
     name:'',
     min:'',
     max:'',
     startTi:'',
     endTi:'',
     departmentId:'',
     userInfo:storage.get('userInfo'),
     historyList:[],
     columns7: [
         {
             title: '编号',
             align: 'center',
             key: 'recordId'
         },
          {
              title: '姓名',
              align: 'center',
              key: 'patientName',
          },
          {
              title: '病历号',
              align: 'center',
              key: 'caseNum'
          },
          {
              title: '科室',
              align: 'center',
              key: 'departmentName'
          },
          {
              title: '病房-床位',
              align: 'center',
              key: 'location'
          },
          {
              title: '启用时间',
              align: 'center',
              key: 'startTime'
          },
          {
              title: '停止时间',
              align: 'center',
              key: 'endTime'
          },
          {
              title: '状态',
              align: 'center',
              key: 'usageState',
              render:(h,params)=>{
                if(params.row.usageState==0){
                  return h('span', {
                    "style":{
                      "color":"#0091FF"
                    },
                },'使用中')
                }else if(params.row.usageState==1){
                  return h('span', {
                    "style":{
                      "color":"rgba(0,0,0,0.50)"
                    }
                },'结束')
                }else{}
                return h('span',params.row.usageState)
            }
          },
          {
              title: '体温记录',
              key: 'action',
              width: 150,
              align: 'center',
              render: (h, params) => {
                  return h('div', [
                    h('img', {
                          domProps: {
                              src:require('../assets/img/tw.png')
                          },
                          style: {
                              color: "#0091FF"
                          },
                          on: {
                              click: () => {
                                  this.showHistory(params)
                                  // showHistory(item.arr.recordId,item.arr.useTimes)
                              }
                          }
                      }),
                  ]);
              }
          }
      ],
      historyList: that.historyList
   }
  },
  computed:{

  },
  beforeCreate(){
    that = this;
  },
  created(){

  },
  mounted:function(){
    if(storage.get('userInfo').role==2){
      that.departmentId = that.userInfo.departmentId
    }else{

    }
    that.getNowTime();
    that.searchHistory();
    that.searchDepartment();

  },
  methods:{
    getNowTime(){
      var date = new Date();
       var year = date.getFullYear();
       var month = date.getMonth() + 1;
       var strDate = date.getDate();

       that.dateChangeStart = year+'-'+month+'-'+strDate;
       that.dateChangeEnd = year+'-'+month+'-'+strDate;
       console.log(that.dateChangeStart)
    },
    changePage(page){
      console.log(page)
      that.currentPage = page;
      that.searchHistory();
    },
    /*检索*/
    search(){
      that.currentPage = 1;
      that.searchHistory();
    },
    /*查询历史记录*/
    searchHistory(){
      var data = {
        currentPage:that.currentPage,
        pageSize:that.pageSize,
      };
      if(that.dateChangeStart!=''){
        data['startDate'] = that.dateChangeStart;
      }
      if(that.dateChangeEnd!=''){
        data['endDate'] = that.dateChangeEnd;
      }
      if(that.name!=''){
        data['patientName'] = that.name;
      }
      if(that.departmentId!=''){
        data['departmentId'] = that.departmentId;
      }
      console.log(data)
      // Message.loading({
      //     content: 'Loading...',
      //     duration: 0
      // });
      that.$get(
        that.global.url+'/patient/record',
        data
      ).then((response) => {
        console.log(response.data)
          // Message.destroy();
        if(response.code==200){

          for(var i=0;i<response.data.list.length;i++){
            if(response.data.list[i].startTime){
              response.data.list[i].startTime = response.data.list[i].startTime.substr(0,response.data.list[i].startTime.length-3);
            }
            if(response.data.list[i].endTime){
              response.data.list[i].endTime = response.data.list[i].endTime.substr(0,response.data.list[i].endTime.length-3);
            }
            response.data.list[i].location = response.data.list[i].roomName +"房"+ response.data.list[i].bedName+'床';
          }
          that.totalPage = Number(response.data.totalPage)*Number(that.pageSize);
          that.historyList = response.data.list;
        }
      })
    },
    /*查询所有科室*/
    searchDepartment(){
      if(storage.get('userInfo').role==2){
        that.$get(
          that.global.url+'/hospital/department',
            {
              departmentId:storage.get('userInfo').departmentId,
            }
        ).then((response) => {
          if(response.code==200){
            that.departmentList = response.data;
            console.log(that.departmentList)
          }
        })
      }else{
        that.$get(
          that.global.url+'/hospital/department',
            {
              departmentId:'',
            }
        ).then((response) => {
          if(response.code==200){
            that.departmentList = response.data;
            console.log(that.departmentList)
          }
        })
      }
    },
    active(departmentId){
      console.log('10',departmentId)
      that.activeDepart = departmentId;
      if(departmentId==-1){
        departmentId = '';
      }
      that.departmentId = departmentId;
        console.log(departmentId)
    },
    showHistory(params){
      console.log(params.row.recordId)
      that.historyrecordId = params.row.recordId;
      that.getHistory();


    },
    /*查看体温记录*/
    getHistory(){
      that.$get(
        that.global.url+'/patient/temperature',
          {
            recordId:that.historyrecordId
          }
      ).then((response) => {
        console.log(response)
        if(response.code==200){
          if(response.data.length==0){

          }else{
            for(var i=0;i<response.data.list.length;i++){
              that.timesList.push(response.data.list[i].dateTime.slice(5,response.data.list[i].dateTime.length));
              that.temperatureList.push(response.data.list[i].temperature);
            }
            // var arrList = that.temperatureList;
            // var max = arrList[0];
            // var min = arrList[0]
            // for(var i = 1; i < arrList.length; i++) {
            //    var cur = arrList[i];
            //    cur > max ? max = cur : null;
            //    var cur = arrList[i];
            //    cur < min ? min = cur : null
            // }
            // that.min = min;
            // that.max = max;
        //     arrList.soft(function(a,b){return a-b;})
        // 　　that.min = arrList[0];
        // 　　that.max = arrList[arrList.length-1];
            that.historyUseTimes = response.data.useTimes;
            console.log(that.timesList)
            console.log(that.temperatureList)
          }
          that.drawLine();
          that.history();
        }else if(response.code==300){
          that.error(response.message)
        }else{
          that.error('网络错误，请重试')
        }
      })
    },
    error (msg) {
      this.$Notice.error({
          title: '操作失败',
          desc: msg
      });
    },
    dateChangeStartFn(d){
      that.dateChangeStart = d;
      console.log('a',that.dateChangeStart)
    },
    dateChangeEndFn(d){
      that.dateChangeEnd = d;
      console.log('b',that.dateChangeEnd)
    },
    drawLine(){
      let myChart = this.$echarts.init(document.getElementById('myChart'));
      myChart.setOption({
         title: {
           text: '已使用'+this.historyUseTimes,
           textStyle:{
              color:'#282D32',
              fontStyle:'normal',
              fontWeight:'normal',
              fontFamily:' PingFangSC-Medium',
      　　　　 fontSize:20,
            },
            left:'45px',
            top:'26px'
          },
         tooltip: {
           trigger: 'axis',
         },
         xAxis: {
            type: 'category',
            // min: 00:00,
            //  max: 41,
            //  interval: 0.5,
            boundaryGap: false,
            axisLine:{symbol:['none', 'arrow']},
            data: that.timesList,
            // data:['07-30 15:00','07-30 15:02','07-30 15:04','07-30 15:06'],
            axisLabel: {
               interval:0,
               rotate:30,
            //    formatter: function (val) {
            //    var date= new Date(val)
            //    var texts=[date.getHours(),date.getMinutes()]
            //    return texts.join(":")
            // },
          }
        },
        yAxis: {
            min: 32,
             max: 41,
             interval: 0.5,
             // data:that.temperatureList,
            axisLabel: {
            formatter: '{value}℃'
          },
            axisLine:{symbol:['none', 'arrow']},
        },
        series: [{
            data: that.temperatureList,
            symbol:'circle',
            formatter: '{value}℃',
            // data:['39.8','37','38','36'],
            type: 'line',
            areaStyle: {
              color: {
                type: 'linear',
                x: 1,
                y: 0,
                x2: 0,
                y2: 0,
                colorStops: [{
                    offset: 0, color: 'rgba(94,119,255,0.65)'
                }, {
                    offset: 1, color: 'rgba(94,119,255,1)'
                }],
                global: false
            }
          },
          lineStyle:{
            color:"rgba(94,119,255,1)",
            width:0
          },
          itemStyle:{
            normal:{
              borderWidth:0,
              color:'rgba(94,119,255,1)',
              borderColor:'rgba(94,119,255,1)',
            }

          },
          markLine:{
            itemStyle: {
              normal: {
                  borderWidth: 1,
                  lineStyle: {
                      type: 'dotted',
                      // color: '#FF9B9B',
                      width: 2,
                  },

                  label: {
                      formatter: '',
                      textStyle: {
                          fontSize: 16,
                          fontWeight: "bolder",
                      },
                  }
              },
            },
            data: [
              {
                  name: '高温',
                  lineStyle: {
                     color: '#FB924B'
                 },
                  yAxis: '37.5'
              },
              {
                  name: '低温',
                  lineStyle: {
                     color: '#007BB6'
                 },
                  yAxis: '36'
              },
            ]
          }
        }]
     });
    },
    show (index) {
      this.$Modal.info({
          title: 'User Info',
          content: `Name：${this.data6[index].name}<br>Age：${this.data6[index].age}<br>Address：${this.data6[index].address}`
      })
    },
    remove (index) {
        this.data6.splice(index, 1);
    },
    add(){
      that.addStatus = !that.addStatus;
    },
    history(){
      that.historyStatus = !that.historyStatus;
      if(that.historyStatus==false){
        that.timesList = [];
        that.temperatureList = [];
      }
    },
    switch(params){
      console.log(params.row)

    },
  },
  components:{
    "Topbar":Topbar,
    'Toast': Toast
  }
}
</script>

<style src="../assets/css/common.scss" lang="scss"></style>
<style src="../assets/css/history.scss" lang="scss"></style>



