<template id="home">

  <div class="mon-all" id="content">
<img src="../assets/img/monitoring/bg.png" alt="" class='bg'>
    <div class="topbar-mon clearfix">
      <div class="topbar-left">
        <div class="return" @click="backHome()">
          <img src="../assets/img/monitoring/out.png" alt="">
          <span>返回首页</span>
        </div>
        <div class="full" @click="fullScreen">
          <img src="../assets/img/monitoring/full.png" alt="">
          <span v-if="fullscreen==false">开启全屏</span>
          <span v-if="fullscreen==true">退出全屏</span>
        </div>
      </div>
      <div class="topbar-middle">
        <h2>{{hospitalName}}体温实时监测平台</h2>
        <h3 v-if='isShow==true'>{{depart}}</h3>
      </div>
      <div class="topbar-right">
        <p>{{date.year}}-{{date.month}}-{{date.strDate}} 星期{{date.week}}</p>
        <p>{{date.hour}}:{{date.minute}}:{{date.second}}</p>
      </div>
    </div>
    <div class="special" v-if='specialLists.length>0 && userInfo.role==2'>
      <div class="special-box"  ref='sli' :style="{ top }">
        <div class="special-item" v-for="item in specialLists">
          <div class="special-detail" >
            <i class='i-red' v-if="item.temperatureStatus=='RED'"></i>
            <i class='i-orange' v-if="item.temperatureStatus=='ORANGE'"></i>
            <span class="cwh">{{item.roomName}}房{{item.bedName}}床</span>
            <span>{{item.patientName}}</span>
            <em class='em-red' v-if="item.temperatureStatus=='RED'">{{item.temperature}}℃</em>
            <em class='em-orange' v-if="item.temperatureStatus=='ORANGE'">{{item.temperature}}℃</em>
          </div>
        </div>
      </div>
    </div>
    <!-- <vue-seamless-scroll :data="specialLists" :class-option="optionSingleHeightTime" class="seamless-warp wrap" v-if='specialLists.length>0 && userInfo.role==2'>
      <div class="wrap-box" v-for="item in specialLists">
        <i class='i-red' v-if="item.temperatureStatus=='BLUE'"></i>
        <i class='i-orange' v-if="item.temperatureStatus=='ORANGE'"></i>
        <span class="cwh">{{item.roomName}}房{{item.bedName}}床</span>
