    <vue-seamless-scroll :data="specialLists" :class-option="optionSingleHeightTime" class="seamless-warp special" style="height:51px;">
        <ul class="item special-box">
            <li class="special-item" v-for="item in specialLists">
              <div class="special-detail">
                <i class='i-red' v-if="item.temperatureStatus=='RED'"></i>
