<template id="home">
  <div class="all">
    <Topbar class="account">
      <img src="../assets/img/home/zhgl.png" alt="">
      <span>账号管理</span>
    </Topbar>
    <div class="choosea clearfix">
      <em @click="add()">新增人员</em>
    </div>
    <div class="account-main">
      <Table border :columns="columns7" :data="managers"></Table>

    </div>
    <Page :total="totalPage" :page-size='pageSize' :current='currentPage' @on-change="changePage" class="pages"/>
    <!-- </div> -->
    <Toast :flow="addStatus">
      <div class="qysb-box add-box">
        <div class="qysb-container">
          <img src="../assets/img/monitoring/guanbi.png" class="close" alt="" @click="add()">
          <div class="title clearfix">
            <img src="../assets/img/device/icon.png" alt="">
            <span>新增人员</span>
          </div>
          <div class="content add-content">
            <form class="" autocomplete='off'>
              <div class="form-item clearfix">
                <span class="sbbh form-name"><em>账号：</em><i>*</i></span>
                <Input class="input" v-model="addAccount"/>
                <!-- <input type="text" class="input" name="" value="" v-model="addAccount"> -->
              </div>
              <div class="form-item clearfix">
                <span class="form-name"><em>密码：</em><i>*</i></span>
                <Input class="input" type="password" v-model="addPassword"/>
                <!-- <input type="password" class="input" name="" value="" v-model="addPassword"> -->
              </div>
              <div class="form-item clearfix">
                <span class="form-name"><em>确认密码：</em><i>*</i></span>
                <Input class="input" type="password" v-model="addConfirm"/>
                <!-- <input type="password" class="input" name="" value="" v-model="addConfirm"> -->
              </div>
              <div class="form-item clearfix">
                <span class="form-name"><em>姓名：</em><i>*</i></span>
                <Input class="input" v-model="addName"/>
                <!-- <input type="text" class="input" name="" value="" v-model="addName"> -->
              </div>
              <div class="form-item clearfix">
                <span class="form-name"><em>手机号码：</em><i>*</i></span>
                <Input class="input" v-model="addPhone"/>
                <!-- <input type="text" class="input" name="" value="" v-model="addPhone"> -->
              </div>
              <div class="form-item clearfix">
                <span class="form-name"><em>岗位：</em></span>
                <Input class="input" v-model="addPost"/>
                <!-- <input type="text" class="input" name="" value="" v-model="addPost"> -->
              </div>
              <div class="form-item clearfix">
                <span class="form-name"><em>性别：</em><i>*</i></span>
                <RadioGroup v-model="sex">
                  <Radio label="男"></Radio>
                  <Radio label="女"></Radio>
                </RadioGroup>
              </div>
              <div class="form-item clearfix">
                <span class="form-name"><em>科室：</em><i>*</i></span>
                <Select class="select" name="" v-model="selectedAdd">
                    <Option :value="item.departmentId" :key="item.name" v-for="item in departmentList" >{{item.name}}</option>
                </Select>
              </div>
            </form>
          </div>
          <div class="ipt-group clearfix">
            <a href="javascript:void(0)" class="on" @click="addManager()">保存提交</a>
            <a href="javascript:void(0)" class="off" @click="add()">取消</a>
          </div>
        </div>
      </div>
    </Toast>
    <Toast :flow="changeStatus">
      <div class="qysb-box change-box">
        <div class="qysb-container">
          <img src="../assets/img/monitoring/guanbi.png" class="close" alt="" @click="change()">
          <div class="title clearfix">
            <img src="../assets/img/device/icon.png" alt="">
            <span>修改信息</span>
          </div>
          <div class="content change-content">
            <form class="">
              <div class="form-item clearfix">
                <span class="form-name"><em>账号：</em><i>*</i></span>
                <span class="form-value">{{managerInfo.account}}</span>
              </div>
              <div class="form-item clearfix">
                <span class="form-name"><em>姓名：</em><i>*</i></span>
                <span class="form-value">{{managerInfo.name}}</span>
              </div>
              <div class="form-item clearfix">
                <span class="form-name"><em>手机号：</em><i>*</i></span>
                <span class="form-value">{{managerInfo.phone}}</span>
              </div>
              <div class="form-item clearfix">
                <span class="form-name"><em>性别：</em><i>*</i></span>
                <span class="form-value">{{managerInfo.sex}}</span>
              </div>
              <div class="form-item clearfix">
                <span class="form-name"><em>岗位：</em><i>*</i></span>
                <Input class="input gw" v-model="poster"/>
                <!-- <input type="text" name="" value="" v-model="poster" class="gw"> -->
              </div>
              <div class="form-item clearfix">
                <span class="form-name"><em>科室：</em><i>*</i></span>
                <Select  class="select" name="" v-model="selected">
                    <Option :value="item.departmentId" :key='item.name' v-for="item in departmentList" >{{item.name}}</option>
                </Select>
              </div>
            </form>
          </div>
          <div class="ipt-group">
            <a href="javascript:void(0)" class="on" @click="putManage()">确认</a>
            <a href="javascript:void(0)" class="off" @click="change()">取消</a>
          </div>
        </div>
      </div>
    </Toast>
  </div>
</template>

<script type="text/javascript">
import Vue from "vue";
import {Message} from 'iview'
var that= {};
import Topbar from '../components/topbar'
import Toast from '../components/toast'
import storage from 'good-storage'
import axios from 'axios';
import md5 from 'js-md5';
export default {
  data(){
   return{
     addStatus:false,
     changeStatus:false,
     departmentList:[],
     sex:'男',
     pageSize:that.global.pageSize,
     currentPage:that.global.currentPage,
     totalPage:that.global.totalPage,
     managerInfo:{},
     selected:'',
     selectedAdd:'',
     addPassword:'',
     addName:'',
     addPhone:'',
     addPost:'',
     addConfirm:'',
     addAccount:'',
     poster:'',
     columns7: [
          {
              title: '姓名',
              align: 'center',
              key: 'name',
          },
          {
              title: '所在科室',
              align: 'center',
              key: 'departmentName'
          },
          {
              title: '岗位',
              align: 'center',
              key: 'post'
          },
          {
              title: '性别',
              align: 'center',
              key: 'sex',

          },
          {
              title: '手机号',
              align: 'center',
              key: 'phone'
          },
          {
              title: '登陆帐户',
              align: 'center',
              key: 'account'
          },
          {
              title: '最近一次登陆',
              align: 'center',
              key: 'loginTime'
          },
          {
              title: '操作',
              key: 'action',
              width: 150,
              align: 'center',
              render: (h, params) => {
                  return h('div', [
                    h('span', {
                          props: {
                              // type: 'primary',
                              // size: 'small'
                          },
                          style: {
                              marginRight: '0.32rem',
                              cursor:'pointer',
                              color: "#0091FF"
                          },
                          on: {
                              click: () => {
                                  that.put(params)
                              }
                          }
                      }, '修改'),
                      h('span', {
                            props: {
                                // type: 'primary',
                                // size: 'small'
                            },
                            style: {
                                marginRight: '0',
                                cursor:'pointer',
                                color: "#EA4849"
                            },
                            on: {
                                click: () => {
                                    this.deleteManage(params)
                                }
                            }
                        }, '删除'),
                  ]);
              }
          }
      ],
      managers: []
   }
  },
  computed:{

  },
  beforeCreate(){
    that = this;
  },
  created(){

  },
  mounted:function(){
    that.searchManager();
    that.searchDepartment();
  },
  methods:{
    changePage(page){
      console.log(page)
      that.currentPage = page;
      that.searchManager();
    },
    /*修改管理员信息*/
    putManage(){
      that.$put(
        that.global.url+'/manage/manager',
          {
            managerId:that.managerInfo.managerId,
            post:that.poster,
            departmentId:that.selected,
          }
      ).then((response) => {
        console.log(response.data)
        if(response.code==200){
          that.change();
          that.searchManager();
          console.log(that.managerInfo)
          if(that.managerInfo.account == storage.get('userInfo').account){
            var arr = that.managerInfo;
            arr.departmentId = that.selected;
            storage.set('userInfo',{});
            storage.set('userInfo',arr);
          }

        }
      })
    },
    /*错误提示*/
    error (msg) {
      this.$Notice.error({
        title: '操作失败',
        desc: msg
      })
    },
    /*新增管理员*/
    addManager(){
      var  re = /^1\d{10}$/;
      if(that.addAccount==''){
        that.error('请输入账号');
      }
      else if(that.addAccount.length>18){
        that.error('账号格式错误，请输入1-18位账号');
      }
      else if(that.addPassword==''){
        that.error('请输入密码');
      }
      else if(that.addPassword.length>12 || that.addPassword.length<6){
        that.error('密码格式错误，请输入6-12位密码');
      }
      else if(that.addConfirm==''){
        that.error('请输入确认密码');
      }else if(that.addPassword!=that.addConfirm){
        that.error('密码与确认密码不符，请重新输入');
      }else if(that.addName==''){
        that.error('请输入姓名');
      }
      else if(that.addName.length>12){
        that.error('姓名格式错误，请输入1-12位姓名');
      }
      else if(that.addPhone==''){
        that.error('请输入手机号');
      }else if(!re.test(that.addPhone)){
        that.error('请输入正确格式手机号');
      }else if(that.selectedAdd==''){
        that.error('请选择科室');
      }else{
        if(that.sex=='男'){
          that.sex = 1;
        }else if(that.sex=='女'){
          that.sex = 0;
        }
        that.$post(
          that.global.url+'/manage/manager',
            {
              account:that.addAccount,
              password:that.addPassword,
              name:that.addName,
              phone:that.addPhone,
              post:that.addPost,
              departmentId:that.selectedAdd,
              sex:that.sex
            }
        ).then((response) => {
          console.log(response.data)
          if(response.code==200){
            that.add();
            that.searchManager();
          }else{
            that.sex = '男'
          }
        })
      }

    },
    /*成功通知*/
    success (msg) {
        this.$Notice.success({
            title: '操作成功',
            desc: msg
        });
    },
    /*删除管理员信息*/
    deleteManage(params){
      that.managerInfo = that.managers[params.index];
      that.$delete(
        that.global.url+'/manage/manager',
        {data:{
          managerId:that.managerInfo.managerId,
        }}

      ).then((response) => {
        console.log(response.data)
        if(response.code==200){
          // that.change();
          that.success();
          that.searchManager();
        }
      })
    },

    /*查询管理员*/
    searchManager(managerId){
      // Message.loading({
      //     content: 'Loading...',
      //     duration: 0
      // });
      that.$get(
        that.global.url+'/manage/manager',
          {
            managerId:managerId,
            role:storage.get('userInfo').role,
            currentPage:that.currentPage,
            pageSize:that.pageSize
          }
      ).then((response) => {
        // Message.destroy();
          if(response.code==200){

            for(var i=0;i<response.data.list.length;i++){
              if(response.data.list[i].sex == 0){
                response.data.list[i].sex = '女'
              }else if(response.data.list[i].sex == 1){
                response.data.list[i].sex = '男'
              }else{}

            }
            that.totalPage = Number(response.data.totalPage)*Number(that.pageSize);
            that.managers = response.data.list;
          }else{

          }
      })
    },
    /*查询所有科室*/
    searchDepartment(){
      that.$get(
        that.global.url+'/hospital/department',
          {
            departmentId:'',
          }
      ).then((response) => {
        if(response.code==200){
          that.departmentList = response.data;
          console.log(that.departmentList)
        }
      })
    },
    /*更改科室*/
    chooseDepartment(){

    },
    /*点击修改按钮,弹框，对修改信息表单赋值*/
    put(params){
      that.managerInfo = that.managers[params.index];
      console.log(that.managerInfo)
      that.poster = that.managerInfo.post;
      console.log(that.poster)
      that.selected = that.managerInfo.departmentId;
      that.change();

    },
    add(){
      that.addStatus = !that.addStatus;
      console.log(that.addStatus)
      if(that.addStatus==false){
        that.addAccount  = '';
        that.addPassword ='';
        that.addConfirm ='';
        that.addName ='';
        that.addPhone ='';
        that.addPost='';
        that.sex = '男'
      }
    },
    change(){
      that.changeStatus = !that.changeStatus;
    },
    switch(params){
      console.log(params.row)

    },
  },
  components:{
    "Topbar":Topbar,
    'Toast': Toast
  }
}

</script>
<style src="../assets/css/common.scss" lang="scss"></style>
<style src="../assets/css/account.scss" lang="scss"></style>



